* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.form-container {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  padding: 10px;
}

.container {
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid rgb(179, 151, 151);
  height: 60vh;
  width: 100%;
  max-width: 1200px;
  margin: 4px;
  padding: 6px;
  border-radius: 5px;
  box-shadow: 3px 4px 9px grey;
  flex-wrap: wrap;
}

.container1 {
  width: 100%;
  height: 77vh;
}
/* Form styling */
.login-form,
.register-form {
  padding: 30px;
  width: 100%;
  max-width: 500px;
}

.img-container {
  display: flex;
  width: 100%;
  height: 100%;
  margin: auto;
}

.img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.head {
  margin-bottom: 10px;
  text-align: center;
  font-family: "Anton", sans-serif;
  font-weight: 600;
}
.form-label {
  padding: 4px;
  margin-bottom: 10px;
  font-size: 16px;
}
.form-input {
  border: 1px solid #682c2c;
  width: 100%;
  padding: 8px;
  margin-bottom: 7px;
  font-size: 14px;
}

.item {
  margin: 20px 0;
}

.form-input::placeholder {
  color: #797777;
  font-size: 16px;
  font-weight: 400;
  opacity: 1;
}

.btn {
  border: none;
  font-family: "Anton", sans-serif;
  font-weight: 600;
  padding: 10px;
  width: 100%;
  text-transform: uppercase;
  background-color: #007bff;
  color: #fff;
  border-radius: 5px;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.btn1 {
  width: 10%;
  display: flex;
  margin: auto;

  justify-content: center;
}

.btn:hover {
  background-color: #0056b3;
}

.ant-form-item-label > label {
  font-family: "Anton", sans-serif;
  font-weight: 600;
}

.fg {
  /* forget passweord text  */
  margin-top: 5px;
  text-align: end;
}
.Lg {
  margin-top: 10px;
  text-align: end;
}

.box1 {
  display: flex;
  margin: auto;
  justify-content: center;
  font-family: "Anton", sans-serif;
  font-weight: 500;
  margin-top: -6px;
}

.text {
  margin-left: 5px;
  color: rgb(212, 41, 41);
  text-decoration: none;
}

.text:hover {
  color: rgb(184, 45, 45);
}

@media (min-width: 700px) {
  .container {
    flex-direction: row;
    justify-content: space-between;
  }

  .login-form,
  .register-form {
    width: 45%;
    padding: 20px;
  }

  .img-container {
    width: 45%;
  }

  .img {
    object-fit: cover;
    width: 100%;
    height: 100%;
  }
}

@media (max-width: 768px) {
  .container {
    flex-direction: column;
    height: auto;
    width: 90%;
  }

  .login-form,
  .register-form {
    width: 80%;
    padding: 20px;
  }

  .img {
    display: none;
  }

  .btn {
    padding: 12px;
  }
  .btn1 {
    font-size: 14px;
    padding: 15px;
    width: 20%;
  }
}

/* Mobile screens */
@media (max-width: 480px) {
  .form-container {
    padding: 15px;
  }

  .container {
    width: 100%;
  }

  .login-form,
  .register-form {
    width: 100%;
    padding: 15px;
  }

  .form-input {
    font-size: 14px;
    padding: 8px;
  }

  .btn {
    font-size: 14px;
    padding: 15px;
  }
  .btn1 {
    font-size: 14px;
    padding: 15px;
    width: 20%;
  }
}

/* Large devices and desktop */
@media (min-width: 1200px) {
  .container {
    width: 60%;
  }

  .login-form,
  .register-form {
    width: 50%;
  }
}

/* Register css------------------------- */
/* Main container */
.otp-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 20px;
  max-width: 400px;
  margin: 0 auto;
  background-color: #fff;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  border-radius: 10px;
}

.otp-box {
  display: flex;
  justify-content: center;
  gap: 10px;
  margin-bottom: 20px;
}

.otp-input {
  width: 50px;
  height: 50px;
  text-align: center;
  font-size: 18px;
  border: 2px solid #ddd;
  border-radius: 5px;
  transition: border-color 0.3s ease;
  border: 1px solid black;
}

.otp-input:focus {
  outline: none;
  border-color: #007bff;
  box-shadow: 0 0 5px rgba(0, 123, 255, 0.5);
}

.resend-container {
  margin-top: 1rem;
  font-size: 0.9rem;
  color: #555;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* Resend OTP Link Styling */
.resend-link {
  color: #007bff;
  text-decoration: none;
  cursor: pointer;
}

.resend-link:hover {
  text-decoration: underline;
}

.resend-link.disabled {
  color: #aaa;
  cursor: not-allowed;
  text-decoration: none;
}

.resend-btn {
  border: none;
  color: red;
  background-color: white;
  margin: 0 0 15px 8px;
  cursor: pointer;
}

.resend-btn:disabled {
  color: #aaa;
  cursor: not-allowed;
}

.timer {
  margin-left: 8px;
  color: #555;
  font-weight: bold;
}

@media (max-width: 768px) {
  .otp-container {
    padding: 15px;
    max-width: 90%;
  }

  .otp-box {
    gap: 5px;
  }

  .otp-input {
    width: 40px;
    height: 40px;
    font-size: 16px;
  }

  .resend-container {
    font-size: 0.8rem;
  }
}

@media (max-width: 480px) {
  .otp-container {
    padding: 10px;
    max-width: 100%;
  }

  .otp-input {
    width: 35px;
    height: 35px;
    font-size: 14px;
  }

  .resend-container {
    flex-direction: column;
    align-items: center;
    gap: 5px;
  }

  .resend-btn {
    margin: 5px 0;
  }
}
